.user__container {
  width: 24%;
  border-radius: 16px;
  display: flex;
  flex: 1 1 24%;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: #fff;
  text-align: center;
  color: rgba(0, 0, 0, 0.87);
  padding: 20px;
  overflow: hidden;
  gap: 18px;
  @media screen and (max-width: 768px) {
    width: 27%;
    flex: 1 1 27%;
  }
  @media screen and (max-width: 480px) {
    width: 100%;
    flex: 1 1 100%;
  }
  img {
    border-radius: 50px;
    width: 70px;
    height: 70px;
  }
  div {
    width: 100%;
  }
}
